body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-container {
    position: relative;
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.25);
    margin-right: 1rem;
    margin-left: 1rem;
    padding: 0 rem 1rem;
    justify-items: center;
    -webkit-align-items: flex-end;
            align-items: flex-end;
    display: -webkit-flex;
    display: flex;
    box-sizing: content-box;
    width: 400px;
}

.container-icon {
    height: 100%;
    pointer-events: none;
    margin-left: .5rem;
    margin-right: .5rem;
}

.container-results {
    position: absolute;
    z-index: 100000;
    margin-top: 2.5rem;
    left: 0;
    right: 0;
    top: 0;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
}

results-page {
    padding: 3rem 1rem;
}

.card {
    width: 345px;
}

.card-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 1rem;
}

.card-media {
    height: 140px;
}

.page-message {
    text-align: center;
}

.details-page {
    padding: 3rem 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.paper-container {
    padding: 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.content {
    padding: 1rem 0rem;
}

.item-image {
    width: 100%;
    height: 250px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.item-loader {
    padding: 1rem;
}

